.search-item {
    border: 1px solid lightgray;
    padding: 10px;
    border-radius: 5px;
    display: flex;
    justify-content: space-between;
    gap: 20px;
    margin-bottom: 20px;
}

.si-img {
    width: 200px;
    height: 200px;
    object-fit: cover;
}

.si-desc {
    display: flex;
    flex-direction: column;
    gap: 10px;
    flex: 2;
}

.si-title {
    font-size: 20px;
    color: #0071c2;
}

.si-distance {
    font-size: 12px;
}

.si-taxi-op {
    font-size: 12px;
    background-color: #008009;
    color: white;
    width: max-content;
    padding: 3px;
    border-radius: 5px;
}

.si-subtitle {
    font-size: 12px;
    font-weight: bold;
}

.si-features {
    font-size: 12px;
}

.si-cancel-op {
    font-size: 12px;
    color: #008009;
    font-weight: bold;
}

.si-cancel-op-subtitle {
    font-size: 12px;
    color: #008009;
}

.si-details {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-between
}

.si-rating {
    display: flex;
    justify-content: space-between
}

.si-rating>span {
    font-weight: 500;
}

.si-rating>button {
    background-color: #003580;
    color: white;
    padding: 5px;
    font-weight: bold;
    border: none;
}

.si-detail-texts {
    text-align: right;
    display: flex;
    flex-direction: column;
    gap: 5px;
}

.si-price {
    font-size: 24px;
}

.si-tax-op {
    font-size: 12px;
    color: gray;
}

.si-check-button {
    background-color: #0071c2;
    color: white;
    font-weight: bold;
    padding: 10px 5px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
}