.popular {
    width: 100%;
    max-width: 1024px;
    display: flex;
    justify-content: space-between;
    gap: 20px;
}

.popular-item {
    flex: 1;
    gap: 10px;
    display: flex;
    flex-direction: column;
}

.popular-img-container {
    display: inline-block;
    /* Needed for ::after */
    position: relative;
    width: 100%;
    height: 250px;
    border-radius: 2px;
}

.popular-img-container::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    box-shadow: inset 0 4px 4px 0 rgba(0, 0, 0, 0.3);
    mix-blend-mode: darken;
    border-radius: 2px;
    z-index: 2;
}

.popular-img {
    width: 100%;
    height: 250px;
    object-fit: cover;
    border-radius: 2px;
}

.popular-name {
    color: #333;
    font-weight: bold;
}

.popular-city {
    font-weight: 300;
}

.popular-price {
    font-weight: 500;
}

.popular-rating>button {
    background-color: #003580;
    color: white;
    border: none;
    padding: 3px;
    margin-right: 10px;
    font-weight: bold;
}

.popular-rating>span {
    font-size: 14px;
}